.loading-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.loading-indicator{
    border: 4px solid rgba(0, 0, 0, 0.1);
    /* border-top: 4px solid #007bff; */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 2s linear infinite;
}

@keyframes spin{
    0%{
        transform:  rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}